import React from 'react'
import './index.css';
import '../bootstrap.css';
import Account from "../accounts"
import {ThemeToggler} from "gatsby-plugin-dark-mode";

  class Profile extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        img: this.props.profilePicture
      }

      this.handleProfilePicture = this.handleProfilePicture.bind(this);
    }

    handleProfilePicture = () =>{
      if(this.state.img===this.props.profilePicture) {
        this.setState({
          img: this.props.onHoverProfilePicture
        })
      }
      else {
        this.setState({
          img: this.props.profilePicture
        })
      }
    };

    render() {


      return (
          <ThemeToggler>
            {({theme, toggleTheme}) => (
                <div className="Block"
                     style={{
                       backgroundColor: 'var(--bg)',
                       color: 'var(--textNormal)',
                       transition: 'color 0.2s ease-out, background 0.2s ease-out',
                     }}>
                  <div className="Cover">
                  </div>
                  <div className="BlockIcon">

                    <img
                        src={this.state.img}
                        onClick={
                          this.handleProfilePicture
                        }
                        onMouseEnter={() => {
                          this.setState({
                            img: this.props.onHoverProfilePicture
                          })
                        }}
                        onMouseOut={() => {
                          this.setState({
                            img: this.props.profilePicture
                          })
                        }}
                    />
                  </div>
                  <br/><br/>
                  <h4>{this.props.name}</h4>
                  <br/>
                  <h6>{this.props.role}</h6>
                  <br/>
                  <p className="BlockPara">
                    I am a Full Stack Developer, Open-Source Contributor, Novice Data Scientist and a Tech Nerd.
                  </p>
                  <Account/>
                </div>
            )}
          </ThemeToggler>
      )
    }
  }

export default Profile;
