import React from 'react'
import { Tooltip } from 'reactstrap';

class Account extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      linkedin: false,
      email: false,
      github: false,
      blog: false,
      resume: false,
      startup: false
    };

    this.linkedIn = this.linkedIn.bind(this);
    this.email = this.email.bind(this);
    this.github = this.github.bind(this);
    this.blog = this.blog.bind(this);
    this.startup=this.startup.bind(this);
    this.resume=this.resume.bind(this);
  }

  linkedIn (){
    this.setState({
      linkedin: !this.state.linkedin
    })
  }
  email(){
    this.setState({
      email: !this.state.email
    })
  }
  github (){
    this.setState({
      github: !this.state.github
    })
  }
  blog(){
    this.setState({
      blog: !this.state.blog
    })
  }
  resume(){
    this.setState({
      resume: !this.state.resume
    })
  }
  startup(){
    this.setState({
      startup: !this.state.startup
    })
  }


  render() {
    return (
      <div style={{position: "relative", top:"-15%"}}>
        <div className="row">
          <div className="col-2">
             <span id="linkedin">
                <a href="https://www.linkedin.com/in/aman-dave/">
                  <img src={require('../../images/linkedin.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.linkedin} target="linkedin" toggle={this.linkedIn}>
                    LinkedIn
                  </Tooltip>
               </span>
          </div>
          <div className="col-2">
             <span id="email">
                <a href="mailto:amandave1999@gmail.com">
                  <img src={require('../../images/email.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.email} target="email" toggle={this.email}>
                    Email
                  </Tooltip>
               </span>
          </div>
          <div className="col-2">
             <span id="startup">
                <a href="https://www.quze.co">
                  <img src={require('../../images/startup.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.startup} target="startup" toggle={this.startup}>
                    Startup
                  </Tooltip>
               </span>
          </div>
          <div className="col-2">
             <span id="github">
                <a href="https://github.com/wizArD-1910">
                  <img src={require('../../images/github.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.github} target="github" toggle={this.github}>
                    GitHub
                  </Tooltip>
               </span>
          </div>
          <div className="col-2">
             <span id="resume">
                <a href="/resume">
                  <img src={require('../../images/resume.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.resume} target="resume" toggle={this.resume}>
                    Resume
                  </Tooltip>
               </span>
          </div>
          <div className="col-2">
             <span id="blog">
                <a href="/blogs">
                  <img src={require('../../images/blogging.png')} alt="" height="40px"/>
                </a>
                  <Tooltip style={{marginTop: "20px"}} placement="bottom" isOpen={this.state.blog} target="blog" toggle={this.blog}>
                    Blog
                  </Tooltip>
               </span>
          </div>
        </div>

      </div>
    );
  }

}

export default Account;
